/* src/pages/Download.css */

.download-container {
  text-align: center;
  padding: 50px;
  background-color: rgba(255, 255, 255, 0.703); /* Semi-transparent white */
}

.download-title, .download-description, .download-info h2, .download-info p {
  color: #000000;
}

.download-title {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.download-description {
  font-size: 1.2rem;
  line-height: 1.6;
  font-weight: bold;
  margin-bottom: 30px;
}

.download-info h2 {
  margin-top: 20px;
  font-size: 1.5rem;
}

.download-info p {
  font-size: 1.1rem;
  margin: 10px 0;
}

.download-button {
  display: inline-block;
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  margin-top: 20px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1.2rem;
}

.download-button:hover {
  background-color: #45a049;
}
