/* src/pages/Home.css */
.home-container {
  text-align: center;
  padding: 50px;
}

.hero-section {
  background-color: rgba(255, 255, 255, 0.518); /* Semi-transparent white */
  border-radius: 8px;
  padding: 40px;
  margin: auto;
  max-width: 800px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.hero-title {
  color: #000000;
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.hero-description {
  color: #000000;
  font-size: 1.2rem;
  line-height: 1.6;
  font-weight: bold;
}
/* Home.css - Add these styles to your existing CSS */

.home-container ,.features-section, .instructions-section {
  background-color: rgba(255, 255, 255, 0.518); /* Semi-transparent white */
  border-radius: 8px;
  padding: 20px;
  margin: 20px auto; /* Adds margin above and below each section */
  max-width: 800px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.section-title {
  color: #000000;
  font-size: 2rem;
  margin-bottom: 15px;
}

.home-container , .features-list, .instructions-list {
  color: #000000;
  text-align: left;
  line-height: 1.6;
}

.features-list li, .instructions-list li {
  margin-bottom: 10px;
}

.instructions-list ol, .instructions-list p, .features-list ul {
  padding-left: 20px; /* Adds padding to list for better alignment */
}

.instructions-section p, .features-section p {
  margin-bottom: 10px;
}

.instructions-section a {
  color: #0000EE; /* Standard link color */
  text-decoration: underline;
}

/* Add this to your existing Home.css file */

.commands-section {
  background-color: rgba(255, 255, 255, 0.518); /* Semi-transparent white */
  border-radius: 8px;
  padding: 40px;
  margin: 20px auto;
  max-width: 800px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 2;
}

.commands-list {
  color: #333;
  text-align: left;
  line-height: 1.6;
  list-style-type: none; /* Removes the default list styling */
  padding-left: 0; /* Aligns the list properly */
}

.commands-list li {
  margin-bottom: 10px;
}

.commands-list code {
  background-color: #f4f4f4;
  border-radius: 4px;
  padding: 2px 6px;
  font-family: monospace;
}
/* Contact.css */


.contact-details {
  background-color: rgba(255, 255, 255, 0.8); /* Semi-transparent white */
  border-radius: 8px;
  padding: 20px;
  margin: 20px auto;
  max-width: 600px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.contact-info p, .contact-rating p {
  margin-bottom: 10px;
}

.contact-rating {
  margin-top: 20px; /* Space between contact info and rating */
}

.rating-stars {
  display: inline-block;
}

.star {
  background-color: transparent;
  border: none;
  cursor: pointer;
  color: orange;
  font-size: 2rem;
}

.star.on {
  color: gold; /* Active star color */
}

.star.off {
  color: lightgray; /* Inactive star color */
}

