/* src/components/NavBar.css */

nav {
  background-color: black;
  padding: 10px 20px;
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: start;
}

nav ul li {
  margin-right: 20px;
}

.nav-spacer {
  flex-grow: 1;
}

nav ul li a {
  color: white;
  text-decoration: none;
  font-size: 18px;
}

nav ul li a:hover {
  color: #ddd;
}
