/* src/pages/Contact.css */

.contact-container {
  text-align: center;
  padding: 50px;
}

.contact-hero-section {
  background-color: rgba(255, 255, 255, 0.518); /* Semi-transparent white */
  border-radius: 8px;
  padding: 40px;
  margin: auto;
  max-width: 800px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.contact-title, .contact-description, .contact-info p {
  color: #000000;
}

.contact-title {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.contact-description {
  font-size: 1.2rem;
  line-height: 1.6;
  font-weight: bold;
}

.contact-info p a {
  color: #000000;
  text-decoration: none;
}

.contact-info p a:hover {
  text-decoration: underline;
}

.rating {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.rating button {
  background: none;
  border: none;
  cursor: pointer;
}

.star {
  font-size: 2rem;
  color: #ffd700; /* Star color */
}

.on .star {
  color: #ffd700; /* Filled star color */
}

.off .star {
  color: #e4e5e9; /* Unfilled star color */
}
